body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

/* Main Css classes */

.button {
  border: none;
  padding: 4px 10px;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  cursor: pointer;
  text-align: center;
  -webkit-transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
}

.button:hover {
  background: #0053ba;
}


.image {
  width: 100%;
  height: auto;
}
.home-page{
}

.musician-list{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: 0 auto;
    -webkit-transition: all 0.7s ease-in-out;
    transition: all 0.7s ease-in-out;
}

.musician-card-container{
    margin: 15px 15px;
    width: 300px;
}
.musician-card{
    width: 300px;
    height: 260px;
    box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, .05);
}

.musician-card-image-conatiner{
    height: 200px;
    display: flex;
    align-items: center;
}

.musician-card-image{
    cursor: pointer;
}

.musician-card-content{
    width: inherit;
    height: 60px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    padding: 6px 6px;
    box-sizing: border-box;
}

.musician-card-data{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}

.musician-card-actions{
    display: flex;
    justify-content: flex-end;
}
.musician-page{
    padding: 0 10%;
}

.musician-page-image-conatiner{
    /* width: 80%; */
    max-width: 1020px;
    margin: 0 auto;
}

.musician-page-content-header{
    position: relative;
    margin: 10px 0;
    font-size: 28px;
}

.musician-page-content-header-title{
    /* font-size: 28px; */
    text-align: center; 
}

.likes-counter{
    position: absolute;
    right: 0px;
    height: inherit;
    top: 0px;
}

.musician-page-content-actions{
    margin: 20px 0;
    display: flex;
    justify-content: flex-end;
}

.musician-page-button{
    margin: 0 0 0 8px;
}

@media only screen and (max-width: 800px) {

    .musician-page{
        padding: 0;
    }

    .musician-page-content{
        padding: 0 10%;
    }

    .musician-page-content-header-title{
        text-align: left;
    }
}
